
  
  .tooltip_box {
    position: absolute;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    padding: 5px;
    border-radius: 5px;
    top: calc(100% + 5px);
    display: none;
  }
  
  .tooltip_box.visible {
    display: block;
  }
  
  .tooltip_arrow {
    position: absolute;
    top: -10px;
    left: 22px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent rgba(0, 0, 0, 0.7) transparent;
  }